
body,a,p,li,ul,h1,h2,h3,h4,h5,strong,div {
		font-size: 0.13rem;
		font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
		margin: 0;
		padding: 0;
		border: none;
		text-decoration: none;
		line-height: 1.5;
		color: rgb(0, 38, 101);
}
ul,li {
		list-style: none;
}
#app {
		height: 100%;
}
body {
		height: 100%;overflow-x: hidden !important; max-width: 640px; margin: 0 auto;background: url('./assets/about_bg.jpg') center center repeat-y; background-size:100% 100%;
}
html {
		height: 100%;
}
.nav {
		position: absolute;
		z-index: 99;
		top: 0.62rem;
		left: 0;
		width: 100%;
}
.nav a {
		display: block;
		height: 0.42rem;
		border-bottom: 0.01rem solid #fff;
		line-height: 0.42rem;
		padding-left: 0.2rem;
		color:#fff;
}
.nav .router-link-exact-active {
		color: #fff;
		background: #0541a5;
}
.layout {
		width: 2.3rem;
		padding-top: 0.1rem;
		overflow: hidden;
}
.layout span {
		font-size: 0.14rem;
		display: block;
		color:#fff;
		text-align: center;
		line-height: 0.5rem;
}
.vux-drawer .actives {
		-webkit-transform: translate3d(2.3rem, 0px, 0px) !important;
		transform: translate3d(2.3rem, 0px, 0px) !important;
}
.vux-drawer .activer {
		-webkit-transform: translate3d(-2.3rem, 0px, 0px) !important;
		transform: translate3d(-2.3rem, 0px, 0px) !important;
}
.vux-drawer>.vux-drawer-content {
		background: #002665 !important;
}
.header {
		height: 0.65rem;
		overflow: hidden;
		width: 100%;
		color: rgb(0, 38, 101);
		background: #eee;
}
.header p {
		font-size: 0.14rem;
		font-weight: bold;
		margin-top: 0.02rem;
}
.header .logo {
		float: left;
		width: 82%;
		padding: 0.1rem 0 0 0.12rem;
		margin-bottom: 0.12rem;
}
.header .logo strong {
		width: 0.44rem;
		height: 0.44rem;
		font-size: 0.15rem;
		float: left;
		margin-right: 0.1rem;
		margin-top: 0.12rem;
}
.header .logo img {
		width: 0.44rem;
		height: 0.44rem;
		font-size: 0.15rem;
		float: left;
		margin-right: 0.1rem;
}
.header .icon {
		float: left;
		width: 0.4rem;
		height: 0.4rem;
		border-radius:50%;
		margin-top: 0.14rem;
		background: #076ee5;
}
.header .icon svg {
		fill: #fff;
		margin: 0.05rem 0;
		width: 0.4rem;
		height: 0.4rem;
		cursor: pointer;
		height: 0.3rem;
}
.footer{
		height:0.38rem;
		background: #ddd;
		width:100%;
		text-align: center;
}
.footer p{
		line-height: 0.38rem;
}
.title {
		text-align: center;
		margin: 0.22rem 0;
		clear: both;
}
.title strong{
		font-size: 0.18rem;
		font-weight: bold;
		color:rgb(0, 38, 101);
}
.title p {
		color:rgb(0, 38, 101);
		line-height: 0.22rem;
		font-size: 0.12rem;
}
.tab{margin: .15rem auto .2rem; overflow: hidden; width:80%;
}
.tab.tabs{display: -webkit-flex;display: -o-flex;display: -ms-flex; display: -webkit-box; display: flex; -webkit-justify-content: center; -o-justify-content: center; -ms-justify-content: center; -webkit-box-pack: center; justify-content: center;
}
.tab a{display: block; width:31%; height: .32rem; background: #999; line-height: .32rem; text-align: center; color: #fff; float: left; margin-right: 2%; margin-bottom:2%; border-radius: .02rem;
}
	
	/* 路由切换动画 */
.child-view {
		width: 100%;
		-webkit-transition: all 0.5s cubic-bezier(.55, 0, .1, 1);
		-webkit-transition: all .5s cubic-bezier(.55, 0, .1, 1);
		transition: all .5s cubic-bezier(.55, 0, .1, 1);
}
.slide-right-enter-active,
	.slide-right-leave-active,
	.slide-left-enter-active,
	.slide-left-leave-active {
		will-change: transform;
		-webkit-transition: all 0.5s;
		transition: all 0.5s;
		height: 100%;
		position: absolute;
		-webkit-backface-visibility: hidden;
		        backface-visibility: hidden;
		-webkit-perspective: 1000;
		        perspective: 1000;
}
.slide-right-enter {
		opacity: 0;
		-webkit-transform: translate3d(-100%, 0, 0);
		        transform: translate3d(-100%, 0, 0);
}
.slide-right-leave-active {
		opacity: 0;
		-webkit-transform: translate3d(100%, 0, 0);
		        transform: translate3d(100%, 0, 0);
}
.slide-left-enter {
		opacity: 0;
		-webkit-transform: translate3d(100%, 0, 0);
		        transform: translate3d(100%, 0, 0);
}
.slide-left-leave-active {
		opacity: 0;
		-webkit-transform: translate3d(-100%, 0, 0);
		        transform: translate3d(-100%, 0, 0);
}



